<template>
    <div class="p-20">

        <div class="filter-container" style="margin-bottom: 20px">
            <span>提问人：</span>
            <el-input class="filter-item" style="width: 300px;" placeholder="请输入提问人" v-model="queryData.questionName">
            </el-input>

            <span>提问类型</span>
            <el-select clearable class="filter-item" v-model="categoryId" placeholder="全部类型" @change=" queryData.categoryId = categoryId ">
              <el-option v-for="status in allListOptions" :key="status.id" :label="status.name" :value="status.id"></el-option>
            </el-select>

            <el-button class="filter-item" size="small" @click="initData">查询</el-button>
        </div>

        <el-table
                ref="singleTable"
                :data="tableData"
                style="width: 100%">
            <el-table-column type="index" label="序号" width="80"></el-table-column>
            <el-table-column
              v-for="item in tableHeader"
              :key="item.prop"
              :label="item.label"
              :prop="item.prop"
              :width="item.width"
              :min-width="item.minWidth"
              :align="item.align"
              :formatter="item.formatter"
              show-overflow-tooltip>
            </el-table-column>

            <el-table-column
              :key="'status'"
              :label="'状态'"
              :prop="'status'"
              :width="120"
              :min-width="120"
              show-overflow-tooltip>

              <template scope="scope">
                <span :class="{
                  colorRed : scope.row.status == 1 , colorGreen : scope.row.status === 2
                }">
                {{ scope.row.status === 1 ? '未解决' : ( scope.row.status === 2 ? '已解决' : '') }}
                </span>
              </template>

            </el-table-column>

            <el-table-column
              v-for="item in tableHeader2"
              :key="item.prop"
              :label="item.label"
              :prop="item.prop"
              :width="item.width"
              :min-width="item.minWidth"
              :align="item.align"
              :formatter="item.formatter"
              show-overflow-tooltip>
            </el-table-column>

            <el-table-column
                    fixed="right"
                    label="操作"
                    width="80">
                <template scope="scope" class="filter-operation">
                    <ul style="list-style: none;padding: 0px">
                        <li style="float:left;width: 30px" v-if="scope.row.status===1">
                            <el-button
                                    type="text" size="small"
                                    @click="getData(scope.row.id)" v-if="permission.reply">回复
                            </el-button>
                        </li>
                        <li style="float:left;width: 30px" v-if="scope.row.status===2">
                            <el-button
                                    type="text" size="small"
                                    @click="getData(scope.row.id)" v-if="permission.reply">查看
                            </el-button>
                        </li>
                        <li style="float:left;width: 30px">
                            <el-button
                                    type="text" size="small"
                                    @click="deleteData(scope.row.id)">删除
                            </el-button>
                        </li>
                    </ul>
                </template>
            </el-table-column>
        </el-table>

        <div class="pagination">
            <el-pagination @size-change="userPageSize" @current-change="userPage"
                           :current-page="queryData.pageNum"
                           :page-sizes="[10,20,30, 50]" :page-size="queryData.pageSize"
                           layout="total, sizes, prev, pager, next, jumper" :total="total">
            </el-pagination>
        </div>

        <el-dialog title="查看" :visible.sync="dialogVisible" @close="dialogClose" :close-on-click-modal="false">
            <el-form :model="data" :rules="rules" ref="data">
                <el-form-item :label-width="itemWidth" label="提问人" prop="userName">
                    <el-input class="input-short" v-model="data.userName" :disabled="true"
                              auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item :label-width="itemWidth" label="提问类型" prop="categoryName">
                    <el-input class="input-short" v-model="data.categoryName" :disabled="true"
                              auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item :label-width="itemWidth" label="提问内容" prop="questionDesc">
                    <el-input class="input-short" v-model="data.questionDesc" :rows="10" type="textarea" :disabled="true"
                              auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item :label-width="itemWidth" label="回复内容" prop="answerDesc">
                    <el-input class="input-short" v-model="data.answerDesc" :rows="10" type="textarea"
                              auto-complete="off"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="dialogVisible = false">取 消</el-button>
                <el-button type="primary" @click="validate">保 存</el-button>
            </div>
        </el-dialog>
    </div>
</template>

<script>

    import fetch from '@/utils/fetch';
    import DCTP from '@/utils/dctp';
    import { userSearch } from '@/api/article'

    export default {
        data() {
            let validate = (rule, value, callback) => {
                if (value === '') {
                    callback(new Error('不能为空'));
                }
                callback();
            };
            return {
                categoryId: '',
                allListOptions: [],
                itemWidth: '160px',
                dialogVisible: false,
                data: {
                    id: '',
                    userName: '',
                    categoryName: '',
                    questionDesc: '',
                    answerDesc: ''
                },
                action: '',
                rules: {
                    answerDesc: [{validator: validate, trigger: 'blur'}]
                },
                queryData: {
                    categoryId: 0,
                    questionName: '',
                    pageNum: 1,
                    pageSize: 10,
                    status: '0'
                },
                total: 0,
                tableData: [],
                tableHeader: [{
                    label: "提问人UID",
                    prop: "userId",
                    width: 120
                }, {
                    label: "提问人姓名",
                    prop: "userName",
                    width: 120
                }, {
                    label: "提问人手机号码",
                    prop: "userPhone",
                    width: 180
                }
                  // ,{
                  //   label: "状态",
                  //   prop: "status",
                  //   width: 120,
                  //   formatter(row, column, value) {
                  //       return value === 1 ? '未解决' : (value === 2 ? '已解决' : '');
                  //   }
                  // }
                ],
                tableHeader2: [
                  {
                      label: "提问类型",
                      prop: "categoryName",
                      width: 120
                  }, {
                      label: "提问内容",
                      prop: "questionDesc",
                      width: 300
                  }, {
                      label: "回复内容",
                      prop: "answerDesc",
                      width: 300
                  }, {
                      label: "回复管理员",
                      prop: "replyName",
                      width: 120
                  }, {
                      label: "创建时间",
                      prop: "createTime",
                      width: 240,
                      formatter(row, column, value) {
                          return DCTP.utils.parseDateTimeStr(value);
                      }
                  }, {
                      label: "回复时间",
                      prop: "updateTime",
                      width: 240,
                      formatter(row, column, value) {
                          return DCTP.utils.parseDateTimeStr(value);
                      }
                  }
                ]
            }
        },
        created() {
          this.getRemoteUserList()
          this.initData();
        },
        methods: {
            getRemoteUserList() {
              userSearch({
                type: 3,
                pageNum: 1,
                pageSize: 10000
              }).then(response => {
                this.allListOptions = response.data.data
                if (!response.result) {
                  this.$message({
                      type: 'warning',
                      message: this.msg
                  })
                }
              })
            },
            validate() {
                this.$refs['data'].validate((valid) => {
                    if (valid) {
                        switch (this.action) {
                            default:
                                this.modify();
                                break;
                        }
                    }
                });
            },
            dialogClose() {
                this.resetData();
            },
            setData(data) {
                let key;
                for (key in this.data) {
                    this.data[key] = data[key] === null ? '' : data[key];
                }
            },
            resetData() {
                let key;
                for (key in this.data) {
                    switch (key) {
                        case 'type':
                            this.data[key] = '2';
                            break;
                        default:
                            this.data[key] = '';
                    }
                }
                this.$refs['data'].resetFields();
                this.action = '';
            },
            getList() {
                let _this = this;
                return new Promise((resolve, reject) => {
                    fetch({
                        url: 'admin/questionanswer',
                        method: 'get',
                        params: _this.queryData
                    }).then(function (response) {
                        if (response.result) {
                            _this.tableData = response.data.data;
                            _this.total = response.data.page.count;
                        }
                    }).catch();
                })
            },
            getData(id) {
                let _this = this;
                return new Promise((resolve, reject) => {
                    fetch({
                        url: 'admin/questionanswer/' + id,
                        method: 'get'
                    }).then(function (response) {
                        if (response.result) {
                            _this.setData(response.data);
                            _this.dialogVisible = true;
                        }
                    }).catch();
                })
            },
            deleteCategory(id) {
                let _this = this;
                return new Promise((resolve, reject) => {
                    fetch({
                        url: 'admin/systemcategory/' + id,
                        method: 'delete'
                    }).then(function (response) {
                        if (response.result) {
                            _this.initData();
                        }
                        _this.getMessage(response.msg, response.result);
                    }).catch();
                })
            },
            showDeleteDialog(id) {
                this.$confirm('确认删除？', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    this.deleteCategory(id);
                }).catch(() => {
                    this.$message.info('操作取消');
                });
            },
            modify() {
                let _this = this;
                return new Promise((resolve, reject) => {
                    fetch({
                        url: 'admin/questionanswer',
                        method: 'put',
                        data: _this.data
                    }).then(function (response) {
                        if (response.result) {
                            _this.dialogVisible = false;
                            _this.initData();
                        }
                        _this.getMessage(response.msg, response.result);
                    }).catch();
                })
            },
            query() {
                let _this = this;
                return new Promise((resolve, reject) => {
                    fetch({
                        url: 'admin/questionanswer/query',
                        method: 'get',
                        params: _this.queryData
                    }).then(function (response) {
                        if (response.result) {
                            _this.tableData = response.data.data;
                            _this.total = response.data.page.count;
                        }
                    }).catch();
                })
            },
            deleteData(id) {
                let _this = this;
                return new Promise((resolve, reject) => {
                    fetch({
                        url: 'admin/deleteQuestion/' + id,
                        method: 'delete'
                    }).then(function (response) {
                        if (response.result) {
                            _this.initData();
                        }
                        _this.getMessage(response.msg,response.result);
                    }).catch();
                })
            },
            initData() {
                if (this.queryData.questionName === '') {
                    this.getList();
                } else {
                    this.query();
                }
            },
            //翻页的按钮
            userPage(val) {
                this.queryData.pageNum = val;
                this.initData();
            },
            //获取每页多少条
            userPageSize(val) {
                this.queryData.pageSize = val;
                this.initData();
            },
        }
    }
</script>

<style>
  .colorRed{
    color: red;
  }
  .colorBlue{
    color: blue;
  }
  .colorGreen{
    color: green;
  }
</style>



