import fetch from '@/utils/fetch'

export function userSearch(name) {
    return fetch({
        url: '/admin/systemcategory',
        method: 'get',
        params: name
    })
}

export function submitform(query) {
    return fetch({
        url: '/admin/helpdoc',
        method: 'post',
        data: query
    })
}

export function getarticleall(query) {
    return fetch({
        url: '/admin/helpdoc/page',
        method: 'post',
        data: query
    })
}

export function fetchArticle(query) {
    return fetch({
        url: '/admin/helpdoc/' + query,
        method: 'get'
    })
}

export function submitalter(query) {
    return fetch({
        url: '/admin/helpdoc',
        method: 'put',
        data: query
    })
}

export function deletearticles(query) {
    return fetch({
        url: '/admin/helpdoc/' + query,
        method: 'delete'
    })
}

export function enabledisable(type,id, status) {
    return fetch({
        url: '/admin/helpdoc/' + type + '/' + id + '/' + status,
        method: 'put'
    })
}


